.header {
  padding-top: 40px;
  padding-bottom: 120px;
  background-image: url("../img/header-bg_2x.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @media (width <= 1024px) {
    padding-top: 26px;
    padding-bottom: 70px;
  }

  @media (width <= 576px) {
    padding-top: 15px;
    padding-bottom: 65px;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 120px;

    @media (width <= 1024px) {
      margin-bottom: 70px;
    }

    @media (width <= 576px) {
      margin-bottom: 65px;
    }
  }

  &__logo {
    @media (width <= 576px) {
      width: 30px;
      height: 30px;
    }
  }

  &__burger {
    display: none;

    @media (width <= 576px) {
      display: block;
    }

    &-phone {
      &:focus {
        outline: none;
      }

      &:focus-visible {
        outline: 2px dashed #fff;
        outline-offset: 2px;
      }
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;

    &-title {
      margin: 0;
      margin-bottom: 36px;
      font-weight: 900;
      font-size: 52px;
      text-align: center;
      color: rgba(255, 255, 255, 0.86);

      @media (width <= 1024px) {
        margin-bottom: 24px;
        max-width: 780px;
        font-size: 40px;
      }

      @media (width <= 768px) {
        margin-bottom: 27px;
        max-width: 580px;
        font-size: 32px;
      }

      @media (width <= 576px) {
        margin-bottom: 23px;
        max-width: 290px;
        font-size: 20px;
      }
    }

    &-descr {
      margin: 0;
      margin-bottom: 90px;
      max-width: 960px;
      font-weight: 400;
      font-size: 22px;
      line-height: 1.40625;
      text-align: center;
      color: rgba(255, 255, 255, 0.75);

      @media (width <= 1024px) {
        margin-bottom: 60px;
        max-width: 754px;
        font-size: 18px;
      }

      @media (width <= 768px) {
        margin-bottom: 60px;
        max-width: 554px;
        font-size: 16px;
      }

      @media (width <= 576px) {
        margin-bottom: 54px;
        max-width: 290px;
        font-size: 14px;
      }
    }
  }
}
