.advantages {
  padding-top: 70px;
  padding-bottom: 70px;

  @media (width <= 576px) {
    padding-top: 30px;
    padding-bottom: 60px;
    background-color: #f9f9f9;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    &-title {
      margin: 0;
      margin-bottom: 25px;
      font-weight: 700;
      font-size: 35px;
      text-align: center;
      color: #383a4c;

      @media (width <= 1024px) {
        margin-bottom: 23px;
        font-size: 32px;
      }

      @media (width <= 576px) {
        margin-bottom: 15px;
        font-size: 20px;
      }
    }

    &-text {
      display: flex;
      align-items: center;
      margin-bottom: 35px;
      font-weight: 500;
      font-size: 22px;
      text-align: center;
      color: #383a4c;

      @media (width <= 1024px) {
        margin-bottom: 33px;
        font-size: 20px;
      }

      @media (width <= 576px) {
        margin-bottom: 26px;
        font-size: 16px;
      }

      &::before {
        content: "";
        display: inline-block;
        margin-right: 23px;
        width: 43px;
        height: 2px;
        background-color: #383a4c;

        @media (width <= 1024px) {
          width: 40px;
        }

        @media (width <= 576px) {
          width: 20px;
        }
      }

      &::after {
        content: "";
        display: inline-block;
        margin-left: 23px;
        width: 43px;
        height: 2px;
        background-color: #383a4c;

        @media (width <= 1024px) {
          width: 40px;
        }

        @media (width <= 576px) {
          width: 20px;
        }
      }
    }

    &-descr {
      margin: 0;
      margin-bottom: 50px;
      max-width: 724px;
      font-weight: 400;
      font-size: 18px;
      line-height: 1.40625;
      text-align: center;
      color: #383a4c;

      @media (width <= 1024px) {
        max-width: 666px;
        font-size: 16px;
      }

      @media (width <= 576px) {
        margin-bottom: 40px;
        max-width: 290px;
        font-size: 12px;
      }
    }
  }

  &__list {
    display: grid;
    column-gap: 50px;
    grid-template-columns: repeat(4, 1fr);

    @media (width <= 768px) {
      column-gap: 15px;
    }

    @media (width <= 576px) {
      display: none;
    }

    &-link {
      display: flex;
      flex-direction: column;
      align-items: center;

      &:focus {
        outline: none;
      }

      &:focus-visible {
        outline: var(--outline);
        outline-offset: var(--outline-offset);
      }
    }

    &-number {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 16px;
      border-radius: 100%;
      width: 122px;
      height: 122px;
      font-weight: 500;
      font-size: 57px;
      line-height: 1;
      text-align: center;
      color: #fff;
      background-color: #23c7ec;

      @media (width <= 1024px) {
        margin-bottom: 26px;
        width: 104px;
        height: 104px;
        font-size: 52px;
      }

      @media (width <= 768px) {
        width: 70px;
        height: 70px;
        font-size: 35px;
      }
    }

    &-text {
      font-weight: 500;
      font-size: 20px;
      line-height: 1.40625;
      text-align: center;
      color: #383a4c;

      @media (width <= 1024px) {
        font-size: 18px;
      }

      @media (width <= 768px) {
        font-size: 13px;
      }
    }
  }

  &__slider {
    display: none;

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    @media (width <= 576px) {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
    }
  }

  &__card {
    margin: 0 auto;
    max-width: 166px;

    &-number {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 26px;
      border-radius: 100%;
      width: 104px;
      height: 104px;
      font-weight: 500;
      font-size: 52px;
      line-height: 1;
      text-align: center;
      color: #fff;
      background-color: #23c7ec;
    }

    &-text {
      font-weight: 500;
      font-size: 18px;
      line-height: 1.40625;
      text-align: center;
      color: #383a4c;
    }
  }

  &__btns {
    position: absolute;
    z-index: 5;
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}
