.btn {
  border-radius: 50px;
  padding: 12px 48px;
  box-shadow: 0 4px 25px 0 rgba(21, 169, 203, 0.3);
  font-weight: 500;
  font-size: 20px;
  text-align: center;
  color: #fff;
  background-color: #15a9cb;
  transition: var(--main-trs);

  @media (width <= 576px) {
    padding: 9px 28px;
    font-size: 16px;
  }

  &:focus {
    outline: none;
  }

  &:focus-visible {
    outline: var(--outline);
    outline-offset: var(--outline-offset);
  }

  @media (any-hover: hover) {
    &:hover {
      background-color: #1392af;
    }
  }

  &:active {
    background-color: #1392af;
  }
}
