.footer {
  padding-top: 48px;
  padding-bottom: 42px;
  background-color: #2f324b;

  @media (width <= 1024px) {
    padding-top: 45px;
    padding-bottom: 45px;
  }

  @media (width <= 576px) {
    padding: 0;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (width <= 768px) {
      flex-wrap: wrap;
    }

    @media (width <= 576px) {
      padding-top: 25px;
      padding-bottom: 25px;
    }
  }

  &__left {
    display: flex;
    column-gap: 20px;
    align-items: center;

    &-logo {
      width: 30px;
      height: 30px;
    }

    &-copyright {
      max-width: 127px;
      font-weight: 500;
      font-size: 12px;
      line-height: 1.40625;
      color: #fff;

      @media (width <= 576px) {
        font-size: 8px;
      }
    }
  }

  &__author {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.40625;
    color: #fff;

    @media (width <= 576px) {
      display: none;
    }

    &-text {
      text-align: center;
    }
  }

  &__bottom {
    display: none;

    @media (width <= 576px) {
      display: block;
      background-color: #2a2c3f;
    }

    &-text {
      padding-top: 15px;
      padding-bottom: 15px;
      font-weight: 500;
      font-size: 12px;
      line-height: 1.40625;
      text-align: center;
      color: #fff;
      opacity: 0.5;
    }
  }
}
