.phone {
  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: 400;
    font-size: 23px;
    text-align: center;
    color: #fff;
    gap: 2px;

    @media (width <= 1024px) {
      font-size: 21px;
      gap: 4px;
    }

    @media (width <= 576px) {
      display: none;
    }

    &:focus {
      outline: none;
    }

    &:focus-visible {
      outline: var(--outline);
      outline-offset: var(--outline-offset);
    }

    &-text {
      border-bottom: 1px solid rgba(255, 255, 255, 0.5);
      font-weight: 400;
      font-size: 16px;
      text-align: center;
      color: #fff;

      @media (width <= 1024px) {
        font-size: 13px;
      }
    }
  }
}
