.questions {
  padding-top: 70px;
  padding-bottom: 70px;
  background-image: url("../img/questions-bg_2x.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @media (width <= 1024px) {
    padding-bottom: 56px;
  }

  @media (width <= 576px) {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;

    @media (width <= 576px) {
      margin-bottom: 35px;
    }

    &-title {
      margin: 0;
      margin-bottom: 17px;
      font-weight: 700;
      font-size: 35px;
      text-align: center;
      color: #fff;

      @media (width <= 1024px) {
        font-size: 32px;
      }

      @media (width <= 576px) {
        margin-bottom: 11px;
        font-size: 20px;
      }
    }

    &-descr {
      font-weight: 500;
      font-size: 22px;
      text-align: center;
      color: rgba(255, 255, 255, 0.8);

      @media (width <= 1024px) {
        font-size: 20px;
      }

      @media (width <= 576px) {
        font-size: 12px;
      }
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
    row-gap: 20px;

    &-input {
      border: 1.38px solid #fff;
      border-radius: 50px;
      padding: 9px 0 9px 25px;
      width: 423px;
      font-weight: 400;
      font-size: 22px;
      color: #fff;
      background-color: transparent;

      @media (width <= 1024px) {
        width: 377px;
        font-size: 20px;
      }

      @media (width <= 576px) {
        width: 290px;
        font-size: 14px;
      }

      &::placeholder {
        color: rgba(255, 255, 255, 0.5);
      }

      &:focus {
        outline: none;
      }
    }
  }

  &__btn {
    display: flex;
    margin: 0 auto 50px;
    padding: 11px 38px;
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    text-align: center;
    color: #fff;

    @media (width <= 1024px) {
      font-size: 17px;
    }

    &-link {
      color: #4a76a8;

      &:focus {
        outline: none;
      }

      &:focus-visible {
        outline: var(--outline);
        outline-offset: 2px;
      }
    }
  }
}
